import { DamageType, TextDamageType } from './data';
export default class Skill {
    /**
     * [constructor description]
     * @param  {String} options.name       []
     * @param  {[type]} options.damageType []
     * @param  {Number} options.baseDamage []
     * @param  {Number} options.addition   []
     * @return {[type]}                    [description]
     */
    constructor ({name = "", code = "", damageType = DamageType.Physical, baseDamage = 0, addition = 100}) {
        this.code = code;
        this.name = name;
        this.damageType = damageType;
        this.baseDamage = baseDamage;
        this.addition = addition;
    }
    getDamage (atk = 0) {
        return this.baseDamage + Math.floor(atk * this.addition / 100);
    }
    getDescribe () {
        return ` ${this.addition} %  ${TextDamageType[this.damageType]}`;
    }
}